import {ADD_ITEM, CHANGE_STATUS, DECREASE_COUNT, DELETE_ITEM, MAIN_STATE, TOTAL_PRICE} from "./type";
import {data} from "../../utils/contstants";


export const getMain = () =>{
    return {
        type:MAIN_STATE,
        payload:data
    }
}

export const changeStatus = (id) =>{
    return {
        type:CHANGE_STATUS,
        payload:id
    }
}


export const addItem = (item) =>{
    return {
        type:ADD_ITEM,
        payload:item
    }
}

export const deleteItem = (item) =>{
    return {
        type:DELETE_ITEM,
        payload:item
    }
}

export const countTotalPrice = (totalPrice) =>{
    return {
        type:TOTAL_PRICE,
        payload:totalPrice
    }
}

export const decreaseCount = (item) =>{
    return {
        type:DECREASE_COUNT,
        payload:item
    }
}
