import React from 'react';
import DeleteIcon from '@mui/icons-material/Delete';
import './CartItem.css'
import {addItem, decreaseCount} from "../../../redux/actions/dispatchActions";
import StarIcon from "@mui/icons-material/Star";
import StarBorderIcon from "@mui/icons-material/StarBorder";


const CartItem = ({item, dispatch, deleteItemUI, changeStatusUI,mainData}) => {

    let status = null
    mainData.find(el=>{
        if(el.id===item.id){
            status = el.select
        }
    })

    return (
        <div className='cart__box'>
            <div className="cart__col">
                <img src={item.img} alt=""/>
            </div>
            <div className="cart__col">
                <p>Price:{item.price} som</p>
                <p>Name:{item.name}</p>
                <p>Quantity: </p>
                <div className='cart__count'>
                    <button
                        onClick={() => dispatch(decreaseCount(item))}
                    >-
                    </button>
                    <span className='cart__quantity'>{item.quantity}</span>
                    <button disabled={item.quantity === item.limit} onClick={() => dispatch(addItem(item))}>+</button>
                </div>
            </div>
            <div className="cart__col">
                <div
                    className="cart__delete"
                    onClick={() => deleteItemUI(item.id)}
                >

                    <DeleteIcon/>
                </div>
                <div onClick={() => changeStatusUI(item.id)}>
                    {status? <StarIcon/> : <StarBorderIcon/>}
                </div>

            </div>
        </div>
    );
};

export default CartItem;