import React, {useEffect,useCallback} from 'react';
import './Cart.css'
import CloseIcon from '@mui/icons-material/Close';
import {useSelector, useDispatch} from "react-redux";
import CartItem from "./CartItem/CartItem";
import {changeStatus, countTotalPrice, deleteItem} from "../../redux/actions/dispatchActions";

const Cart = ({toggle, handleToggle}) => {

    const {cartItems, totalPrice} = useSelector(state => state.cart)
    const dispatch = useDispatch()

    const {mainData} = useSelector(state=>state.main)

    const deleteItemUI = (id) => {
        dispatch(deleteItem(id))
    }

    console.log(cartItems)

    const changeStatusUI = (id) =>{
        dispatch(changeStatus(id))
    }


    const countTotalPriceUI = useCallback(() => {
        const price = cartItems.reduce((acc, rec) => {
            return acc + rec.quantity * rec.price
        }, 0)
        dispatch(countTotalPrice(price))
    },[dispatch,cartItems])

    useEffect(() => {
        countTotalPriceUI()
    }, [countTotalPriceUI])

    const items = cartItems.map(el =>
        <CartItem
            key={el.id}
            item={el}
            dispatch={dispatch}
            deleteItemUI={deleteItemUI}
            changeStatusUI={changeStatusUI}
            mainData={mainData}
        />
    )

    return (
        <div className={toggle ? 'cart__modal show' : 'cart__modal'}>
            <p
                className="exit"
                onClick={handleToggle}
            ><CloseIcon/></p>
            <div className="cart__content">
                <h2>Total:{totalPrice} soms</h2>
                {cartItems.length > 0
                    ? items
                    : <h2>Cart is Empty</h2>
                }
            </div>
        </div>
    );
};

export default Cart;