import {ADD_ITEM, DECREASE_COUNT, DELETE_ITEM, TOTAL_PRICE} from "../actions/type";


const initialState = {
    cartItems: [],
    totalPrice: 0
}


const cartReducer = (state = initialState, action) => {
    const {type, payload} = action
    switch (type) {
        case ADD_ITEM:
            const index = state.cartItems.findIndex(el => el.id === payload.id)

            if (index >= 0) {
                if(state.cartItems[index].limit===state.cartItems[index].quantity){
                    alert(`you can't add more than ${state.cartItems[index].limit} items`)
                }else{
                    state.cartItems[index].quantity += 1
                }
                return {...state, cartItems: [...state.cartItems]}
            } else {
                const initialQuantity = {...payload, quantity: 1}
                return {
                    ...state, cartItems: [...state.cartItems, initialQuantity]
                }
            }
        case DECREASE_COUNT:
            const indexD = state.cartItems.findIndex(el => el.id === payload.id)
            if (state.cartItems[indexD].quantity > 1) {
                state.cartItems[indexD].quantity -= 1
                return {
                    ...state,
                    cartItems: [...state.cartItems]
                }
            } else if (state.cartItems[indexD].quantity === 1) {
                const filteredCart = state.cartItems.filter(el => el.id !== payload.id)
                return {
                    ...state,
                    cartItems: filteredCart
                }
            } else {
                return
            }
        case DELETE_ITEM:
            const filteredCart = state.cartItems.filter(el => el.id !== payload)
            return {
                ...state,
                cartItems: filteredCart
            }
        case TOTAL_PRICE:
            return {...state, totalPrice: payload}
        default:
            return state
    }
}

export default cartReducer