import {CHANGE_STATUS, MAIN_STATE} from '../actions/type'

const initialState = {
    mainData:[]
}

const mainReducer = (state = initialState, action) => {
    const {type, payload} = action
    switch (type) {
        case MAIN_STATE:

            return {...state,mainData: payload}
        case CHANGE_STATUS:
            console.log(state.mainData)
        const changeName = state.mainData.map(el=>{
            if(el.id===payload){
                el.select=!el.select
            }
            return el
        })
            return {...state,mainData: changeName}
        default:
            return state
    }
}
export default mainReducer

