import React,{useEffect} from 'react';
import './Main.css'
import Item from "./Item/Item";
import {data} from "../../utils/contstants";
import {useDispatch,useSelector} from "react-redux";
import {addItem, changeStatus, getMain} from "../../redux/actions/dispatchActions";

const Main = () => {

    const dispatch = useDispatch()

    const {mainData} = useSelector(state=>state.main)

    const addItemUI = (item) =>{
        dispatch(addItem(item))
    }

    const changeStatusUI = (id) =>{
        dispatch(changeStatus(id))
    }

    useEffect(()=>{
        if(mainData.length<1){
            dispatch(getMain())
        }
    },[])

    const items = mainData.map(el=>
        <Item
            key={el.id}
            name={el.name}
            img={el.img}
            id={el.id}
            price={el.price}
            select={el.select}
            item={el}
            addItemUI={addItemUI}
            changeStatusUI={changeStatusUI}
        />
    )


    return (
        <section id='main'>
            <div className="container">
                <h2>Restaurant</h2>
                <div className="row">
                    {items}
                </div>
            </div>
        </section>
    );
};

export default Main;